import './billboardsSelect.css'
import { store } from '../index'
import { billboardsChangeColor, billboardMaxPointOnMapChanged, billboardPinLenChanged,
    billTypeAdd,billBordTypeSelected } from '../store/oppertunityData';


let billboards = null;

const perparingBillboardsSelectControl = (data) => {
    billboards = data;
    var myParent = document.body;
    var div = document.createElement('div');
    div.setAttribute("id", "selectItem");
    myParent.appendChild(div);
    div.innerHTML = "<svg width='15' height='15'>" +
    "<rect width='15' id='billboardColorIndicator' height='15' style='fill:" + data[0].color +
    ";stroke-width:3;stroke:rgb(0,0,0)' />" +
    "</svg>";
    //console.log(store.getState());
    //console.log(store.getState());
    store.dispatch(billboardsChangeColor({billboardColor: data[0].color}));
    store.dispatch(billboardMaxPointOnMapChanged({billboardMaxPointOnMap: data[0].count}));
    // store.dispatch(billTypeAdd({type: data[0].code}));
    store.dispatch(billBordTypeSelected({billboardType: data[0].code}));
    var selectList = document.createElement("select");
    selectList.id = "billboardsSelect";
    selectList.className = "form-control";
    selectList.onchange = _onChange;
    div.appendChild(selectList);
    for (var i = 0; i < data.length; i++) {
        var option = document.createElement("option");
        option.value = data[i].code;
        option.text = data[i].name;
        selectList.appendChild(option);
    }
    return this;
}

const _onChange = (event) =>{
    let billboardIndex = billboards.findIndex(item => item.code == event.target.value);
    const colorIndicator = document.getElementById("billboardColorIndicator");
    colorIndicator.style.fill = billboards[billboardIndex].color;
    store.dispatch(billboardsChangeColor({billboardColor: billboards[billboardIndex].color}));
    store.dispatch(billboardMaxPointOnMapChanged({billboardMaxPointOnMap: billboards[billboardIndex].count}));
    store.dispatch(billboardPinLenChanged({lenOfPinMarkerOnMap: 0}));
    store.dispatch(billBordTypeSelected({billboardType: billboards[billboardIndex].code}));
    //console.log(store.getState());
}

export {perparingBillboardsSelectControl}